<template>
    <div class="m-3">
        <!-- <v-container id="dashboard" fluid tag="section" > -->
            <v-row class="m-3">
                <v-col cols="12" sm="6" lg="4">
                    <base-material-stats-card color="blue" icon="mdi-poll"
                    title="Soldes " :value="solde" sub-icon="mdi-tag"
                    sub-text="Solde Actuel de l'agence"
                    />
                </v-col>
                <v-col cols="12" sm="6" lg="4">
                    <base-material-stats-card color="primary" icon="mdi-poll"
                    title="Versement" :value="versement" sub-icon="mdi-tag"
                    sub-text="Total versements sur le mois"
                    />
                </v-col>
                <v-col cols="12" sm="6" lg="4">
                    <base-material-stats-card color="red" icon="mdi-poll"
                    title="Dépenses" :value="depense" sub-icon="mdi-tag"
                    sub-text="Total des dépenses sur le mois"
                    />
                </v-col>
            </v-row>
        <!-- </v-container> -->

        <div> 
            <div class="row">
                <div class="col-3" v-for="item in button_add_config" :key="item.id">
                    <v-btn elevation="1" color="default" class="mr-2" @click="loadConfigChamp(item)"> <v-icon left >mdi-plus</v-icon>{{item.libelle}}</v-btn>
                </div>
            </div> 

            <RegularTableHeaderAdd v-bind:title="title" v-bind:headers="headers" v-bind:items="items" />
        </div>


        <BaseModal v-bind:dialog="dialog" v-bind:data_champ="data_champ" v-bind:Modal_title="Modal_title" v-bind:type="type" v-bind:loadConfig="loadConfig"/>

    </div>

</template>

<script>

    import axios from 'axios';
    import moment from 'moment';
    import NProgress from 'nprogress';
    import RegularTableHeaderAdd from '@/components/Tables/RegularTableHeaderAdd.vue';
    import BaseModal from '@/components/Modal/baseCardModalComponent.vue';

    export default {

        components: {
            RegularTableHeaderAdd,
            BaseModal,
        },

        data() {

            return{

                title: 'Historique des opérations',
                dialog: false,
                button_add_config: [],
                data_champ: [],
                Modal_title: '',
                type: null,
                solde: null,
                versement: null,
                depense: null,

                headers: [
                    {
                    sortable: true,
                    text: 'Journée concernée',
                    value: 'date',
                    },
                    {
                    sortable: true,
                    text: 'Montant',
                    value: 'montant',
                    },
                    {
                    sortable: true,
                    text: 'Auteur',
                    value: 'auteur',
                    },
                    {
                    sortable: true,
                    text: 'Type',
                    value: 'type',
                    },
                    {
                    sortable: true,
                    text: "Date d'enregistrement",
                    value: 'created_at',
                    }
                    
                ],
                items: [],
            }
        },

        created() {
            this.loadConfig();
        },

        methods: {

            async loadConfig() {

                NProgress.start();
                try {

                    const response = await axios.get('xperf/index');
                    
                    this.button_add_config = response.data.configType;
                    this.solde = response.data.solde;
                    this.versement = response.data.versement;
                    this.depense = response.data.depense;

                    this.dialog = false;

                    this.loadDataTab();

                } 
                catch (error) {
                        this.$notify({
                        type: 'error',
                        title: "Echec de recuperation des informations "
                    });
                }

                NProgress.done();

            },
            
            async loadConfigChamp(config) {

                NProgress.start();

                try {

                    let crm = {
                        id: config.id
                    };

                    const response = await axios.post('xperf/getConfigChampEtat', crm);
                    var res = response.data.configChamp;
                    
                    this.data_champ = res;
                    this.type = config.id;
                    this.Modal_title = config.libelle;
                    this.dialog = true;


                } 
                catch (error) {
                        this.$notify({
                        type: 'error',
                        title: "Echec de recuperation des informations "
                    });
                }

                NProgress.done();

            },

            async loadDataTab() {

                NProgress.start();

                this.items = [];
                
                try {

                    const response = await axios.get('xperf/getShortOp');
                    var res = response.data.data;

                    res.forEach(element => {

                      let crm = {
                        created_at: element.created_at,
                        date : element.date,
                        montant: element.montant,
                        auteur: element.auteur,
                        type: element.libelle,
                      };
                    
                      this.items.push(crm);
                    });


                } 
                catch (error) {
                        this.$notify({
                        type: 'error',
                        title: "Echec de recuperation des informations"
                    });
                }

                NProgress.done();

            },

        },
    }
</script>